import {
  ToolLineProps,
  ToolGroupProps,
} from './interface';
import { judgeShouldShow } from '../Toolbar/util';

export const renderToolArray = (props: ToolLineProps | ToolGroupProps, key: string, target, renderFunc: Function, option?: any) => {
  const { showTargets, hideTargets } = props;
  const shouldShow = judgeShouldShow(target, showTargets, hideTargets);
  if (!shouldShow) return null;
  const contents = (props[key] || []).map((tb, idx) => renderFunc(tb, idx, option)).filter(item => item);
  if (!contents.length) return null;
  return contents;
};
